<template>
    <ul>
        <li v-for="item in getItems(textt)">
            <input type="checkbox"
                   v-model="item.isChecked"
                   tabindex="-1">

            {{ item.text }}
        </li>
    </ul>
</template>

<script>
  const getItems = text => text.split('\n')
    .filter(t => t.trim().length)
    .map(t => (t.startsWith('[ ] ') || t.startsWith('[x] ')) ? t : `[ ] ${ t }`)
    .map(t => {
      let checkbox = t.slice(0, 3);
      return ({
        checkbox,
        text: t.substr(3, t.length),
        isChecked: checkbox === '[x]'
      })
    });

  export default {
    data: () => ({
      textt: `
 lorem
ipsum
    ipsum ipsumipsumipsum ipsum 4
lorem
`,
    }),
    methods: {
      getItems,
    },
  }
</script>